import React, { useState, useEffect } from "react"
import { motion } from "framer-motion";
import axios from "axios"
import { AccountId } from "@hashgraph/sdk";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const uri = `https://testnet.mirrornode.hedera.com/api/v1`;
const instance = axios.create({
  baseURL: uri,
})

const NftSlider = (props) => {
  var settings = {
    dots: false,
    arrows: false,
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 1500,
    cssEase: "linear",
    responsive: [
      {
        breakpoint: 1160,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 1,
          swipeToSlide: true,
        },
      },
      {
        breakpoint: 950,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          swipeToSlide: true,
        },
      },
      {
        breakpoint: 750,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 550,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 470,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 370,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          variableWidth: true,
        },
      },
    ],
  };

  const [walletnfts, setWalletNfts] = useState([]);
  const [nftmeta, setNftmeta] = useState([])
  const [balance, setBalance] = useState("")
  const accountId = AccountId.fromString(props.accountId)
  let value = [], metadata=[];

	const ws = new WebSocket("wss://localhost:8000");
	useEffect(() => {
  
	  ws.onopen = function () {
		console.log("Connected to WebSocket server");
	  };
  
	  ws.onmessage = function (event) {
		console.log("Message received:", event.data);
		if (accountId) {
		  ws.send([accountId , balance, ]);
		}
	  };
  
	  ws.onclose = function () {
		console.log("Disconnected from WebSocket server");
	  };
  
	  return () => {
		ws.close();
	  };
	}, [accountId , ws , balance]);

  useEffect(() => {
    const load = async () => {
      if (walletnfts.length > 0) {
        console.log("Already imported")
      } else {
        let nftaccounts = await instance.get(`accounts/${accountId}/nfts`);
        let balances = await instance.get(`balances?account.id=${accountId}`);
        console.log("balances: ", parseFloat(balances.data.balances[0].balance)/100000000);
        setBalance(parseFloat(balances.data.balances[0].balance)/100000000)
        console.log("nftaccounts: ", nftaccounts)
        let nfts = nftaccounts.data.nfts;
        for (var i = 0; i < nfts.length; i++) {
          const currentvalue = nfts[i].metadata;
          const newValue = Buffer.from(currentvalue, "base64");
  
          const res = await fetch(`https://ipfs.io/ipfs/${newValue}`);
          const meta = await res.json();
          meta.image = meta.image.slice(7)
          // console.log(meta);
          value[i] = "https://ipfs.io/ipfs/" + meta.image;
          console.log(value[i])
          metadata.push(meta)
        }
  
        setWalletNfts(value)
        setNftmeta(metadata);
      }
      Sendnft(metadata, accountId)
  
    }

    load();
  }, [accountId, balance])

  console.log("walletnfts: ", walletnfts)
  console.log("NFTmetadata: ", nftmeta)

  const Sendnft = (metadata, accountId) => {
    var arr = {
      accountId: accountId,
      metadata: metadata
    }

    axios.post("/users/nftsave", arr).then((res) => {
      console.log(res.data);
      if (res.data === "success") alert("success");
      else if (res.data === "Already send") alert("Already send");
    });
  }

  return (
    <>
      <motion.h1
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{
          ease: "easeInOut",
          duration: 0.9,
          delay: 0.1,
        }}
        className="font-general-regular 
                      text-center text-ternary-dark
                      dark:text-white
                      text-3xl
                      sm:text-4xl
                      pt-20
                      font-bold
                      ">
        My NFT Collections
      </motion.h1>
      <motion.button
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{
          ease: "easeInOut",
          duration: 0.9,
          delay: 0.1,
        }}
        className="cursor-pointer bg-transparent bg-gradient-to-r from-purple-700 to-pink-600
        hover:text-gray-300 px-8 rounded-md"

      >
        <motion.h1
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{
            ease: "easeInOut",
            duration: 0.9,
            delay: 0.1,
          }}
          className="font-general-regular 
                      text-center text-ternary-dark
                      dark:text-white
                      font-bold mt-1  
                      ">
          NFT Send
        </motion.h1>
      </motion.button>
      <motion.section
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
        className="flex flex-col items-center md:mt-12 mx-auto sm:flex-row sm:justify-between">
        <motion.div
          initial={{ opacity: 0, y: -180 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ ease: "easeInOut", duration: 0.9, delay: 0.2 }}
          className="float-right mt-8 sm:mt-0 sm:w-full text-right w-full">
          <Slider {...settings} className="mx-3 my-10 slider">
            {
              walletnfts.map((nft, index) => {
                return <div className="px-3" key={index}>
                  <div className="bg-white dark:bg-ternary-dark rounded-xl shadow-lg slider-card">
                    <div className="slider-img w-full">
                      <img
                        src={nft}
                        alt=""
                        className="2xl:h-290 h-290 lg:h-186 md:h-203 object-cover rounded-t-xl sm:h-4 w-full xl:h-242"
                      />
                    </div>
                    <h1 className="dark:text-white font-bold p-3 text-black text-center">
                      SpaceKitty
                    </h1>
                  </div>
                </div>
              })
            }
 
          </Slider>
        </motion.div>
      </motion.section>
    </>
  );



};
export default NftSlider;
